import React from 'react'
import logo1 from './img/svg-gobbler 2.png'
import logo2 from './img/svg-gobbler (5) 2.png'
import logo3 from './img/_x34_1_Online_Payment.png'
import './style.scss'

export default function Section6() {
  return (
    <div> 
    <div className="container2">
    <div className="section6">
      
      <h4>Стоимость Обучение </h4>
      <div className="sect6__cards">
        <div className="sect6_card1">
          <img src={logo1} alt="" />
          <p>Весь курс разбит на несколько блоков. Оплата поэтапная вы платите только за тот блок, который сейчас проходите.
</p>
        </div>
        <div className="sect6_card1">
          <img src={logo2} alt="" />
          <p>Любой из блоков вы можете оплатить в рассрочку</p>
        </div>
        <div className="sect6_card1">
          <img src={logo3} alt="" />
          <p>Если передумаете учиться, то возврат можно оформить в любой момент. Возвращаем деньги за 3 рабочих дня.</p>
        </div>
      </div>
    </div> 

    </div>
      
    </div>
  )
}
