import React from "react";
import "./style.scss";
import headerimg from "./img/header-logo.png";
import logo from "./img/logo.svg";
import arrow from './img/Arrow1.svg'
import { useMediaQuery } from 'react-responsive';

export default function Navbar() {
  
  const isDesktopOrLaptop = useMediaQuery({ minDeviceWidth: 990 });


  const textStyle = {
    fontSize: '46px',

    lineHeight: '46px',
    letterSpacing: '0em',
    textAlign: 'left',
    fontStyle: 'italic',
        
  };
  const braceStyle = {
    color: '#f15525',
  };

  const text = (
    <span>
      <span style={braceStyle}>{'{'}</span>
      программирования
      <span style={braceStyle}>{'}'}</span>
    </span>
  );
  return (
    <div>
      <div className="header-bg"></div>
      <nav class="navbar navbar-expand-lg  container2 navbar__2">
        <div class={` navbar-items   ${isDesktopOrLaptop ? 'container2' : 'container-fluid'}`}>
          <div className="navbar__logo-left mr-auto">
            <a class="navbar-brand" href="#">
              <img src={logo} alt="" />
            </a>
            
<div className="nav__right-btn-block2 nav__right-btn-block2-mobile">
                <button><svg width="10" height="9" viewBox="0 0 10 9" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M4.77588 0.429688L8.71157 4.36551L4.77588 8.30132" stroke="white"/>
<path d="M8.73218 4.36731H0.151367" stroke="white"/>
</svg> Войти
</button>
              </div>

            <button
              class="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span class="navbar-toggler-icon"></span>
            </button>
            
          </div>
          
          <div class="collapse navbar-collapse nav__text-mobile" id="navbarSupportedContent">
            <ul class="navbar-nav me-auto mb-2 mb-lg-0">
              <li class="nav-item">
                <a class="nav-link active" aria-current="page" href="#">
                  Чему вы научитесь
                </a>
              </li>
              <li class="nav-item">
                <a class="nav-link active" aria-current="page" href="#">
                Процесс обучения
                </a>
              </li>
              <li class="nav-item">
                <a class="nav-link active" aria-current="page" href="#">
                Стоимость
                </a>
              </li>
              <li class="nav-item">
                <a class="nav-link active" aria-current="page" href="#">
                Контакты
                </a>
              </li>
            </ul>
            <div className="nav__right-btn me-2 mb-0 pb-0">
              <div className="nav__right-btn-block1 ">
                <p>Регистрация</p>
                
              </div>
              <div className="nav__right-btn-block2">
                <button><svg width="10" height="9" viewBox="0 0 10 9" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M4.77588 0.429688L8.71157 4.36551L4.77588 8.30132" stroke="white"/>
<path d="M8.73218 4.36731H0.151367" stroke="white"/>
</svg> Войти
</button>
              </div>
            </div>
          </div>
        </div>
      </nav>
      <div className="container2">
        <div className="header__common-block">
          <div className="header__block-left" >
            <h1>
              Школа <span className="header__block-left-text-mobile" style={textStyle}>{text}</span> для тех, кому нужны реальные навыки, а не просто сертификат
            </h1>
            <div className="header__left-text">
              
            <p>Пройдите тестирование, чтобы получить доступ <br /> к бесплатным вводным урокам</p>
            </div>
            <button className="header__btn-left">Пройти тестирование <svg width="15" height="15" viewBox="0 0 15 15" fill="none" xmlns="http://www.w3.org/2000/svg">
<path d="M0.292893 13.2929C-0.0976311 13.6834 -0.0976311 14.3166 0.292893 14.7071C0.683418 15.0976 1.31658 15.0976 1.70711 14.7071L0.292893 13.2929ZM15 0.999969C15 0.447684 14.5523 -3.1092e-05 14 -3.13449e-05L5 -3.05441e-05C4.44772 -3.08813e-05 4 0.447685 4 0.999969C4 1.55225 4.44772 1.99997 5 1.99997L13 1.99997L13 9.99997C13 10.5523 13.4477 11 14 11C14.5523 11 15 10.5523 15 9.99997L15 0.999969ZM1.70711 14.7071L14.7071 1.70708L13.2929 0.292862L0.292893 13.2929L1.70711 14.7071Z" fill="white"/>
</svg>
 </button>
          </div>
          <div className="header__block-right">
            <img src={headerimg} alt="" />
          </div>
        </div>
      </div>
    </div>
  );
}
